  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"), url("../fonts/Montserrat/MontserratMedium.woff2") format("woff2"), url("../fonts/Montserrat/MontserratMedium.woff") format("woff"), url("../fonts/Montserrat/MontserratMedium.ttf") format("ttf"), url("/fonts/Montserrat/MontserratMedium.eot") format("eot");
  font-weight: 500;
  font-style: normal;
