  src: local("Oswald"), url("../fonts/Oswald/OswaldBold.woff2") format("woff2"), url("../fonts/Oswald/OswaldBold.woff") format("woff"), url("../fonts/Oswald/OswaldBold.ttf") format("ttf"), url("/fonts/Oswald/OswaldBold.eot") format("eot");
