@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto/RobotoBold.woff2") format("woff2"), url("../fonts/Roboto/RobotoBold.woff") format("woff"), url("../fonts/Roboto/RobotoBold.ttf") format("ttf"), url("/fonts/Roboto/RobotoBold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
body {
  margin: 0;
  background-color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #434242;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1248px;
  padding: 0 24px;
  margin: 0 auto;
}

/* Components */
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 112px;
}

.logo {
  margin-right: 20px;
  flex-shrink: 0;
  width: 80px;
  height: 80px;
}

.site-nav {
  display: flex;
}
.site-nav__item {
  margin-right: 33px;
}
.site-nav__item:last-child {
  margin-right: 12px;
}
.site-nav__link {
  padding: 3px 10px;
  font-weight: 500;
  color: #fff;
  position: relative;
}
.site-nav__link:after {
  content: "";
  display: block;
  width: calc(100% - 20px);
  height: 2px;
  position: absolute;
  top: 100%;
  left: 50%;
  background-color: #961914;
  opacity: 0;
  transform: translateX(-50%);
  transition: opacity 0.2s linear;
}
.site-nav__link:hover:after {
  opacity: 1;
}

.burger {
  display: none;
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 800px;
  margin-bottom: 80px;
  color: #fff;
  background: #434242 url("../images/intro-bg.jpg") center no-repeat;
  background-size: cover;
  position: relative;
}
.intro__inner {
  width: 100%;
  max-width: 460px;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}
.intro__title {
  margin-bottom: 25px;
  width: 100%;
  max-width: 340px;
  font-size: 20px;
  font-weight: 400;
  order: 2;
}
.intro__subtitle {
  margin-bottom: 12px;
  font-family: "Oswald", sans-serif;
  font-size: 54px;
  font-weight: 700;
  line-height: 1.3;
  letter-spacing: 0.02em;
  order: 1;
}
.intro__text-mobile {
  display: none;
}
.intro__decorative {
  height: 98px;
  position: absolute;
  top: 63px;
  left: 156px;
  z-index: -1;
  transform: rotate(5deg);
}
.intro__btn {
  margin-right: auto;
  order: 3;
}
.intro__zooov {
  width: 168px;
  height: 548px;
  position: absolute;
  top: 160px;
  left: calc(100% - 147px);
  z-index: 2;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 13px 42px;
  font-size: inherit;
  font-weight: 600;
  line-height: 1.4;
  color: #fff;
  text-align: center;
  border: 0;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s linear;
}
.btn--primary {
  background-color: #961914;
}
.btn--primary:hover {
  background-color: #75110D;
}